.input-group {
  position: relative;
  &--eye {
    width: 20px;
    height: 20px;
    position: absolute;
    right: 10px;
    z-index: 1000;
    top: 13px;
  }
}
.input {
  padding: 10px;
  width: 100%;
  box-sizing: border-box;
  outline: none;
  background: #ffffff;
  border: 1px solid #dcdbdd;
  border-radius: 12px;
  font-weight: 500;
  font-size: 14px;
  line-height: 21px;
  color: #202020;
  resize: none;
  &error {
    border: 1px solid #cc0033;
  }
}
.placeholder {
  position: absolute;
  top: 13px;
  left: 8px;
  padding: 0px 5px;
  transition: 0.3s;
  pointer-events: none;
  font-weight: 400;
  font-size: 12px;
  line-height: 17px;
  color: #84818a;
}
.placeholder.multi {
  top: 30px;
}
.input:not(:placeholder-shown) + .placeholder,
.input:focus + .placeholder {
  top: -10px;
  color: #8a63f4;
  background-color: #fff;
  border-radius: 10px;
}

.input:not(:placeholder-shown) + .placeholder.multi,
.input:focus + .placeholder.multi {
  top: 5px;
  color: #8a63f4;
  background-color: #fff;
  border-radius: 10px;
}
.input.error {
  border: 1px solid #cc0033;
}
.error-message {
  outline: none;
  color: #cc0033;
  font-size: 10px;
}
