$primaryColor: #202020;
$secondaryColor: #dcdbdd;
$accentColor: #8a63f4;

.button {
  border-radius: 12px;
  padding: 11px 35px;
  border: 1px solid $secondaryColor;
  position: relative;
  display: flex;
  overflow: hidden;
  color: $primaryColor;
  cursor: pointer;
  align-items: center;
  justify-content: center;
  align-self: center;
  background: #ffffff;
  transition: border 1s ease-out;
  &__image {
    margin-left: 10px;
    margin-right: 10px;
    margin-top: auto;
    margin-bottom: auto;
    align-items: center;
    justify-content: center;
    display: flex;
  }
}
.button:hover {
  border: 1px solid $accentColor;
}
.button .button-text {
  font-weight: 600;
  font-size: 16px;
  text-align: center;
  text-transform: capitalize;
}
