.auth-container {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  &--con {
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    flex: 1;
  }
  &--inner-container {
    background: #fbfcfc;
    border-radius: 32px;
    display: flex;
    flex-direction: column;
    padding: 20px;
    width: 70%;
    position: relative;
    overflow: hidden;
  }
}
